@import './colors';
@import './fonts';


.bg-cover{
    background-position: center center;
    background-size: cover;
}


body{
  background: #4DCFE0;
  font-family: $font-base;
}

.topBanner{
    width: 100%;
    height: 600px;
    position: relative;
}

.topBanner .container{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}


//banner image
.bannerImage{
    display: block;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -20;
}

.bannerImage-cover{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(269.54deg, rgba(55, 55, 55, 0.6) 60.78%, rgba(0, 0, 0, 0) 100.42%);
    z-index: -10;
}

@media (max-width: 767px){
    .bannerImage{
        display: none;
    }
}

.header-text{
    text-align: end;
    color: $white;
}

h1{
    display: inline-block;
    padding-left: 4rem;
    padding-bottom: 25px;
    border-bottom: 1px solid #4DCFE0;
    position: relative;
    text-align: right;
    color: $white;
}

h1::after{
    content: '';
    display: block;
    width: 50%;
    height: 6px;
    background-color: #4DCFE0;
    position: absolute;
    bottom: -3px;
    right: 0;
}

.thumbnail-area{
    width: 40%;
    display: flex;
    justify-content: flex-end;
}

.thumbnail{
    flex: 1;
    margin-top: 70px;
    padding-left: 5px;
    padding-right: 5px;
    cursor: pointer;
    
    & img{
        width: 100%;
        height: 90px;
        object-fit: cover;
    }

}

#index-carousel{
    display: none;
}

@media (max-width: 767px){
    header .container{
        position: relative;
    }

    .header-text{
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        padding-top: 20px;
        padding-bottom: 50px;
        text-align: center;
    }

    h1{
        padding: 0;
        border-bottom: 0;
        text-align: center;
    }

    h1::after{
        display: none;
    }

    .thumbnail-area{
        display: none;
        width: 100%;
        height: 100%;
        position: relative;
    }

    #index-carousel{
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: -10;
    }

    .carousel-inner{
        width: 100%;
        height: 100%;
    }

    .carousel-item{
        height: 100%;

        & img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}

//main
.index-post-area {
    & .zh-en-heading{
        margin-top: 0;
    }
}

.index-post-area{
    margin-top: $section-margin-y;
    margin-bottom: $section-margin-y;
}

.content-row.read-more{
    justify-content: flex-end;
    align-items: flex-end;
    margin-top: $heading-margin-y;

    & .btn-lg{
        font-family: $eng-font-base;
    }
}

@media (max-width: 767px){
    .content-row.read-more{
        display: flex;
        justify-content: center;
    }
}

// 推手計劃介紹
.hands-intro{
    padding-top: $section-margin-y;
    padding-bottom: $section-margin-y;
    position: relative;
    background-color: rgba(#EDEFF1, .2);

    &::before{
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-image: url('../../assets/images/index/about-bg.png');
        background-size: cover;
        background-position: center center;
        z-index: -5;
    }

    &::after{
        content: '';
        display: block;
        z-index: -10;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: url('../../assets/images/index/about-bg.png'), linear-gradient(90deg, rgba(255, 229, 59, 0.6) -99.72%, #FF2525 100%);
        background-size: cover;
        background-position: center center;
        background-blend-mode: screen;
        mix-blend-mode: luminosity;
        opacity: 0.5;
        
    }

    & .zh-en-heading{
        margin-top: 0;
        color: $white;

        & h3{
            line-height: 1.5;
        }

        & .en-sub{
            font-size: $h5-size;
            color: #EDEFF1;
        }
    }
}


.hands-intro-text p{
    margin-bottom: $in-body-margin-y;
    color: $white;
}

.hands-intro-more{
    margin-right: 0;
}

//填寫表單
.join-us{
    padding-top: $section-margin-y;
    padding-bottom: $section-margin-y;
    background-color: #EDEFF1;

    & .en-sub{
        font-size: $h5-size;
    }
}

.join-us-main{
    justify-content: center;

    & .threeCol-tile{
        padding-left: 40px;
        padding-right: 40px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }


    & .h5{
        margin-bottom: 20px;
    }

    & .description{
        margin-bottom: 40px;
    }
}